@import "../../App.scss";

.project-list{
   padding: 50px 100px; 
   display: flex;
   flex-direction: column;
   align-items: center;
   text-align: center;
}

.project-list-text{
    width: 65%;
}

.project-list-title{
    font-size: 50px;
    font-weight: 600;

}

.project-list-description{
    margin: 20px 0;
}

.project-list-body{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}